.list {
    list-style: none;
    padding: 0;
    font-weight: 500;
    font-size: 18px;
    border-top: 1px solid lightgrey;
    }
.list li {
display: flex;
align-items: center;
justify-content: space-between;
border-bottom: 1px solid lightgrey;
padding-left: 12px;
padding-right: 12px;
}
.activeTimer {
    color: darkgreen;
    background-color: rgba(239, 249, 229, 0.3);
}

.timer {
    background-color: inherit;
}

.div {
    display: flex;
    align-items: center;
    
}
.time {
margin-right: 18px;
}
.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 50%;
    border-color: inherit;
    height: 30px;
    width: 30px;
    background-color: inherit;
    margin-left: 8px;
}

.btn:hover,
.btn:focus {
    background-color: gainsboro;
}

.icon {
    padding-top: 4px;
}