.form {
position: relative;
}

.input {
    width: 85%;
    height: 30px;
    border-radius: 50px;
    margin-top: 5px;
    margin-bottom: 10px;
    padding-left: 15px;
    padding-right: 45px;
}
@media screen and (min-width: 320px) and (max-width: 559px) {
    .input {
    width: 80%;
    }
}

.btn {
    width: 32px;
    height: 32px;
    cursor: pointer;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 19px;
    right: 22px;
    background-color: green;
    border-color: inherit;
}
@media screen and (min-width: 320px) and (max-width: 559px) {
    .btn {
right: 0;
    }
}

.btn:hover,
.btn:focus {
    background-color: darkgreen;
}

.icon {
width: 30px;
height: 30px;
}